/**
 * DOM,id
 * @param id id
 */
export const judgeDOMExitAndCreateDOM = (id: string) => {
  let domWrapper = document.querySelector(`#${id}`);
  if (domWrapper === null) {
    domWrapper = document.createElement("div");
    domWrapper.setAttribute("id", id);
    document.body.append(domWrapper);
  }
  return domWrapper
}
