import React from "react"
import Button from "../button/Button"
import Message from "./Message"

interface DemoType {
  type?: "info" | "success" | "error" | "warning",
  duration?: number,
  onClose?: boolean,
  icon?: string
}

export const Demo: React.FC<DemoType> = (props) => {

  const handleNormalClick = () => {
    props.type === 'info' && !props.onClose && Message.info(".")
    props.type === 'success' && !props.duration && Message.success(".")
    props.type === 'error' && Message.error(".")
    props.type === 'warning' && !props.icon && Message.warning(".")

    props.type === 'success' && props.duration && Message.success(".", {
      duration: 4000
    })

    props.type === 'info' && props.onClose && Message.info(".", {
      onClose: () => {
        alert("")
      }
    })

    props.type === 'warning' && props.icon && Message.warning(".", {
      icon: "gift"
    })
  }
  return (
    <Button onClick={handleNormalClick}>{props.type}</Button>
  )
}

